@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */

:root{
  --yellow: #feb60a;
  --red: #ff0062;
  --blue: #00dbf9;
  --violet: #da00f7;
}

* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
  color: #f1356d;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover {
  color: #f1356d;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* blog previews / list */

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;

}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;

}

/* loading */
.container > div{
  -webkit-animation: bounce 1.5s 0.5s linear infinite;
          animation: bounce 1.5s 0.5s linear infinite;
  border-radius: 100%;
  margin: 2vw;
}

.container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.yellow{
  background-color: #feb60a;
  background-color: var(--yellow);
  width: 1.5vw;
  height: 1.5vw;
}

.red{
  background-color: #ff0062;
  background-color: var(--red);
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
  width: 2vw;
  height: 2vw;
    
}

.blue{
  background-color: #00dbf9;
  background-color: var(--blue);
  -webkit-animation-delay: 1.4s;
          animation-delay: 1.4s;
  width: 3vw;
  height: 3vw;
  
}

.violet{
  background-color: #da00f7;
  background-color: var(--violet);
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  width: 3.5vw;
  height: 3.5vw;
}

@-webkit-keyframes bounce {
  0%, 50%, 100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
  }
  25% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-filter: blur(3px);
            filter: blur(3px);
  }
  75% {
    -webkit-filter: blur(5px);
            filter: blur(5px);
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}

@keyframes bounce {
  0%, 50%, 100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
  }
  25% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-filter: blur(3px);
            filter: blur(3px);
  }
  75% {
    -webkit-filter: blur(5px);
            filter: blur(5px);
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}
.blog-preview a{
text-decoration: none;
}

/* blog details page */
.blog-details h2 {
  font-size: 20px;
  color:#f1356d;
  margin-bottom: 10px;
}

.blog-details div {
  margin: 20px 0;
}

 button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* create new blog form */

.create {

  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label {
  text-align: left;
  display: block;
}

.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;

}

.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
